import { ResumeData, Chunk } from '../types';

// Enhanced retrieval system with context awareness
// In production, this would use vector embeddings and cosine similarity

export interface SearchResult {
  chunks: Chunk[];
  confidence: number;
}

/**
 * Enhanced chunk creation with better categorization
 */
export const createChunks = (resumeData: ResumeData): Chunk[] => {
  const chunks: Chunk[] = [];
  let chunkId = 0;

  // Process basics with enhanced context
  if (resumeData.basics && resumeData.basics.name) {
    const basicInfo = [
      `Name: ${resumeData.basics.name}`,
      resumeData.basics.label ? `Title: ${resumeData.basics.label}` : '',
      resumeData.basics.phone ? `Phone: ${resumeData.basics.phone}` : '',
      resumeData.basics.email ? `Email: ${resumeData.basics.email}` : '',
      resumeData.basics.location ? `Location: ${resumeData.basics.location.address}` : ''
    ].filter(Boolean).join('. ');
    
    chunks.push({
      id: `chunk-${chunkId++}`,
      content: basicInfo,
      section: 'basics',
      source: 'Contact Information',
      score: 0
    });
  }

  // Process education with enhanced context
  if (resumeData.education && Array.isArray(resumeData.education)) {
    resumeData.education.forEach((edu) => {
      const educationInfo = [
        `Education: ${edu.studyType || 'Degree'} ${edu.area ? `in ${edu.area}` : ''}`,
        `Institution: ${edu.institution || 'Unknown Institution'}`,
        edu.startDate && edu.endDate ? `Duration: ${edu.startDate} to ${edu.endDate}` : '',
        edu.score ? `Academic Performance: ${edu.score}` : ''
      ].filter(Boolean).join('. ');
      
      chunks.push({
        id: `chunk-${chunkId++}`,
        content: educationInfo,
        section: 'education',
        source: `Education - ${edu.institution}`,
        score: 0
      });
    });
  }

  // Process work experience with enhanced context
  if (resumeData.work && Array.isArray(resumeData.work)) {
    resumeData.work.forEach((work) => {
      const workInfo = [
        `Position: ${work.position || 'Unknown Position'}`,
        `Organization: ${work.name || 'Unknown Organization'}`,
        work.location ? `Location: ${work.location}` : '',
        work.startDate && work.endDate ? `Duration: ${work.startDate} to ${work.endDate}` : '',
        work.supervisor ? `Supervisor: ${work.supervisor}` : ''
      ].filter(Boolean).join('. ');
      
      chunks.push({
        id: `chunk-${chunkId++}`,
        content: workInfo,
        section: 'work',
        source: `Work Experience - ${work.name}`,
        score: 0
      });

      // Add separate chunks for work responsibilities/highlights
      if (work.highlights && work.highlights.length > 0) {
        work.highlights.forEach((highlight) => {
          chunks.push({
            id: `chunk-${chunkId++}`,
            content: `${work.position} at ${work.name}: ${highlight}`,
            section: 'work',
            source: `Work Responsibilities - ${work.name}`,
            score: 0
          });
        });
      }
    });
  }

  // Process references
  if (resumeData.references && Array.isArray(resumeData.references)) {
    resumeData.references.forEach((ref) => {
      const referenceInfo = [
        `Reference: ${ref.name}`,
        `Title: ${ref.position}`,
        `Organization: ${ref.organization}`,
        `Phone: ${ref.phone}`,
        `Email: ${ref.email}`
      ].filter(Boolean).join('. ');
      
      chunks.push({
        id: `chunk-${chunkId++}`,
        content: referenceInfo,
        section: 'references',
        source: `Reference - ${ref.name}`,
        score: 0
      });
    });
  }

  return chunks;
};

/**
 * Enhanced search with query-type awareness
 */
export const searchChunks = (query: string, chunks: Chunk[]): SearchResult => {
  const lowerQuery = query.toLowerCase();
  const queryWords = lowerQuery.split(/\s+/).filter(word => word.length > 2);
  
  // Define boost factors for different query types
  const contextBoosts = {
    strengths: ['education', 'work', 'skills', 'achievements'],
    weaknesses: ['work', 'education', 'development'],
    clinical: ['work', 'clinical', 'therapy', 'treatment'],
    research: ['work', 'research', 'dissertation', 'publication'],
    academic: ['education', 'academic', 'research'],
    personal: ['basics', 'contact', 'background'],
    experience: ['work', 'clinical', 'research', 'teaching']
  };
  
  // Determine query context for boosting
  let queryContext = 'general';
  if (/strength|good|excel|positive|skilled|competent/.test(lowerQuery)) {
    queryContext = 'strengths';
  } else if (/weakness|challenge|improve|develop|growth|area.*development/.test(lowerQuery)) {
    queryContext = 'weaknesses';
  } else if (/clinical|therapy|treatment|patient|client/.test(lowerQuery)) {
    queryContext = 'clinical';
  } else if (/research|dissertation|study|methodology/.test(lowerQuery)) {
    queryContext = 'research';
  } else if (/education|degree|school|university|academic/.test(lowerQuery)) {
    queryContext = 'academic';
  } else if (/experience|background|work|position/.test(lowerQuery)) {
    queryContext = 'experience';
  }
  
  const scoredChunks = chunks.map(chunk => {
    let score = 0;
    const chunkContent = chunk.content.toLowerCase();
    
    // Base scoring: word matches
    queryWords.forEach(word => {
      if (chunkContent.includes(word)) {
        score += 1;
      }
    });
    
    // Context-based boosting
    const relevantSections = contextBoosts[queryContext as keyof typeof contextBoosts] || [];
    if (relevantSections.some(section => chunk.section.includes(section) || chunkContent.includes(section))) {
      score *= 1.5;
    }
    
    // Specific keyword boosting
    const keywordBoosts = {
      'john': 1.2,
      'britton': 1.2,
      'psychology': 1.3,
      'clinical': 1.3,
      'therapy': 1.3,
      'research': 1.2,
      'education': 1.2,
      'experience': 1.2,
      'supervision': 1.3,
      'training': 1.3,
      'internship': 1.4,
      'appic': 1.4
    };
    
    Object.entries(keywordBoosts).forEach(([keyword, boost]) => {
      if (chunkContent.includes(keyword)) {
        score *= boost;
      }
    });
    
    // Section-specific boosting
    const sectionBoosts = {
      'basics': 1.1,
      'education': 1.2,
      'work': 1.3,
      'clinical': 1.4,
      'research': 1.3,
      'references': 1.1
    };
    
    const sectionBoost = sectionBoosts[chunk.section as keyof typeof sectionBoosts] || 1.0;
    score *= sectionBoost;
    
    return {
      ...chunk,
      score: score
    };
  });
  
  // Sort by score and return top results
  const sortedChunks = scoredChunks
    .filter(chunk => chunk.score > 0)
    .sort((a, b) => b.score - a.score)
    .slice(0, 8); // Limit to top 8 results
  
  const confidence = sortedChunks.length > 0 ? 
    Math.min(sortedChunks[0].score / 5, 1.0) : 0;
  
  return {
    chunks: sortedChunks,
    confidence: confidence
  };
};

/**
 * Enhanced retrieval with query-aware filtering
 */
export const getRelevantChunks = async (query: string, resumeData: ResumeData): Promise<Chunk[]> => {
  const chunks = createChunks(resumeData);
  const searchResult = searchChunks(query, chunks);
  
  // Apply minimum threshold
  const threshold = 0.5;
  const relevantChunks = searchResult.chunks.filter(chunk => chunk.score >= threshold);
  
  return relevantChunks.slice(0, 6); // Return top 6 most relevant chunks
};

/**
 * Enhanced formatting for display
 */
export const formatChunkForDisplay = (chunk: Chunk): string => {
  return `**${chunk.source}**: ${chunk.content}`;
};

/**
 * Enhanced source information
 */
export const getSourceInfo = (chunk: Chunk): string => {
  return `${chunk.source} (${chunk.section})`;
}; 