import React from 'react'
import ReactDOM from 'react-dom/client'
import App from './App.tsx'
import './styles/globals.css'

console.log('Starting CV application...');

try {
  const rootElement = document.getElementById('root');
  
  if (!rootElement) {
    throw new Error('Root element not found');
  }

  console.log('Root element found, creating React app...');
  
  const root = ReactDOM.createRoot(rootElement);
  
  root.render(
    <React.StrictMode>
      <App />
    </React.StrictMode>,
  );
  
  console.log('React app rendered successfully');
} catch (error) {
  console.error('Failed to start application:', error);
  
  // Fallback error display
  const rootElement = document.getElementById('root');
  if (rootElement) {
    rootElement.innerHTML = `
      <div style="padding: 20px; font-family: Arial, sans-serif;">
        <h1 style="color: #ef4444;">Application Error</h1>
        <p>The CV application failed to start.</p>
        <details style="margin-top: 20px;">
          <summary style="cursor: pointer;">Error Details</summary>
          <pre style="background: #f3f4f6; padding: 10px; border-radius: 4px; font-size: 12px; overflow: auto;">
            ${error instanceof Error ? error.toString() : 'Unknown error'}
          </pre>
        </details>
        <button onclick="window.location.reload()" style="margin-top: 20px; padding: 10px 20px; background: #3b82f6; color: white; border: none; border-radius: 4px; cursor: pointer;">
          Reload Page
        </button>
      </div>
    `;
  }
} 