/**
 * Run a single ensemble model
 */
async function runEnsembleModel(model: string, prompt: string, context: string, query: string): Promise<string> {
  try {
    const response = await openai.chat.completions.create({
      model,
      messages: [
        { role: "system", content: prompt },
        { role: "assistant", content: context },
        { role: "user", content: query }
      ],
      ...BASE_CONFIG
    });
    
    const content = response.choices[0].message.content || "";
    
    // Calculate cost
    const inputTokens = response.usage?.prompt_tokens || estimateTokens(context + query);
    const outputTokens = response.usage?.completion_tokens || estimateTokens(content);
    const cost = calculateCost(model, inputTokens, outputTokens);
    
    sessionCost += cost;
    console.info(`[Ensemble] Model: ${model}, Cost: $${cost.toFixed(4)}, Session Total: $${sessionCost.toFixed(4)}`);
    
    return content;
  } catch (error) {
    console.error(`[Ensemble] Error with ${model}:`, error);
    return ""; // Return empty string on error
  }
}

/**
 * Run ensemble of specialized models
 */
async function runEnsemble(context: string, query: string): Promise<EnsembleResponse> {
  const ensemblePromises = [
    runEnsembleModel(ENSEMBLE_MODELS.strengths, ENSEMBLE_PROMPTS.strengths, context, query),
    runEnsembleModel(ENSEMBLE_MODELS.weaknesses, ENSEMBLE_PROMPTS.weaknesses, context, query),
    runEnsembleModel(ENSEMBLE_MODELS.clinical, ENSEMBLE_PROMPTS.clinical, context, query),
    runEnsembleModel(ENSEMBLE_MODELS.research, ENSEMBLE_PROMPTS.research, context, query),
    runEnsembleModel(ENSEMBLE_MODELS.academic, ENSEMBLE_PROMPTS.academic, context, query),
    runEnsembleModel(ENSEMBLE_MODELS.general, ENSEMBLE_PROMPTS.general, context, query)
  ];
  
  const responses = await Promise.all(ensemblePromises);
  
  return {
    strengths: responses[0],
    weaknesses: responses[1],
    clinical: responses[2],
    research: responses[3],
    academic: responses[4],
    general: responses[5]
  };
}

/**
 * Compile ensemble responses into final answer
 */
async function compileEnsembleResponses(ensembleResponses: EnsembleResponse, query: string): Promise<string> {
  // Filter out empty responses
  const validResponses = Object.entries(ensembleResponses)
    .filter(([, value]) => value && value.trim().length > 0)
    .map(([key, value]) => `${key.toUpperCase()}:\n${value}`)
    .join('\n\n');
  
  if (!validResponses) {
    throw new Error("No valid ensemble responses to compile");
  }
  
  try {
    const response = await openai.chat.completions.create({
      model: ENSEMBLE_MODELS.compiler,
      messages: [
        { role: "system", content: COMPILER_PROMPT },
        { role: "user", content: `Original Query: ${query}\n\nEnsemble Responses:\n${validResponses}` }
      ],
      ...BASE_CONFIG,
      max_tokens: 512 // Allow longer final response
    });
    
    const content = response.choices[0].message.content || "";
    
    // Calculate cost
    const inputTokens = response.usage?.prompt_tokens || estimateTokens(validResponses + query);
    const outputTokens = response.usage?.completion_tokens || estimateTokens(content);
    const cost = calculateCost(ENSEMBLE_MODELS.compiler, inputTokens, outputTokens);
    
    sessionCost += cost;
    console.info(`[Compiler] Model: ${ENSEMBLE_MODELS.compiler}, Cost: $${cost.toFixed(4)}, Session Total: $${sessionCost.toFixed(4)}`);
    
    return content;
  } catch (error) {
    console.error(`[Compiler] Error:`, error);
    // Fallback: combine responses manually
    return validResponses.split('\n\n').slice(0, 2).join('\n\n') + '\n\n* John Britton CV Bot*';
  }
}

/**
 * Enhanced model selection with ensemble support
 */
function selectModel(query: string): string {
  const classification = classifyQuery(query);
  
  // Use ensemble for complex queries if enabled and within budget
  if (classification.useEnsemble) {
    return 'ensemble'; // Special identifier for ensemble processing
  }
  
  // Use premium model for strengths/weaknesses and complex analytical queries
  if (classification.isStrengthsWeaknesses || classification.complexity >= 3) {
    return sessionCost < ROUTING_CONFIG.costLimitPerSession * 0.8 ? FALLBACK_MODELS.premium : FALLBACK_MODELS.complex;
  }
  
  // Use complex model for analytical queries
  if (classification.type === 'analytical' && classification.complexity >= 2) {
    return FALLBACK_MODELS.complex;
  }
  
  // Use primary model for most queries
  return FALLBACK_MODELS.primary;
}

/**
 * Calculate estimated cost for a request
 */
function calculateCost(model: string, inputTokens: number, outputTokens: number): number {
  const costs = MODEL_COSTS[model as keyof typeof MODEL_COSTS];
  if (!costs) return 0;
  
  return (inputTokens / 1000000) * costs.input + (outputTokens / 1000000) * costs.output;
}

/**
 * Estimate token count (rough approximation)
 */
function estimateTokens(text: string): number {
  return Math.ceil(text.length / 4); // Rough approximation: 4 chars per token
}

/**
 * Get appropriate system prompt based on query context
 */
export function getSystemPrompt(query: string): string {
  const classification = classifyQuery(query);
  return SYSTEM_PROMPTS[classification.context as keyof typeof SYSTEM_PROMPTS] || SYSTEM_PROMPTS.general;
}

/**
 * Enhanced chat function with ensemble support
 */
export async function chat(messages: any[], chunks: any[] = [], query: string = '') {
  const selectedModel = selectModel(query);
  const systemPrompt = getSystemPrompt(query);
  
  // Check if ensemble should be used
  if (selectedModel === 'ensemble') {
    const context = chunks.map(chunk => `[${chunk.id}] ${chunk.content}`).join('\n');
    
    // Run ensemble
    const ensembleResponses = await runEnsemble(context, query);
    
    // Compile responses
    const finalResponse = await compileEnsembleResponses(ensembleResponses, query);
    
    return {
      choices: [{ message: { content: finalResponse } }],
      modelUsed: 'ensemble',
      cost: 0, // Cost already tracked in individual models
      sessionCost: sessionCost
    };
  }
  
  // Standard single-model processing
  const enhancedMessages = [
    { role: "system", content: systemPrompt },
    ...messages.slice(1) // Skip original system message
  ];
  
  try {
    const response = await openai.chat.completions.create({
      model: selectedModel,
      messages: enhancedMessages,
      ...BASE_CONFIG
    });
    
    const content = response.choices[0].message.content || "";
    
    // Calculate and track cost
    const inputTokens = response.usage?.prompt_tokens || estimateTokens(enhancedMessages.map(m => m.content).join(' '));
    const outputTokens = response.usage?.completion_tokens || estimateTokens(content);
    const cost = calculateCost(selectedModel, inputTokens, outputTokens);
    
    sessionCost += cost;
    
    console.info(`[AI] Model: ${selectedModel}, Cost: $${cost.toFixed(4)}, Session Total: $${sessionCost.toFixed(4)}`);
    
    return {
      ...response,
      modelUsed: selectedModel,
      cost: cost,
      sessionCost: sessionCost
    };
    
  } catch (error: any) {
    console.error(`[AI] Error with ${selectedModel}:`, error);
    
    // Fallback to simpler model on error
    if (selectedModel !== FALLBACK_MODELS.fallback) {
      console.info(`[AI] Falling back to ${FALLBACK_MODELS.fallback}`);
      
      try {
        const fallbackResponse = await openai.chat.completions.create({
          model: FALLBACK_MODELS.fallback,
          messages: enhancedMessages,
          ...BASE_CONFIG
        });
        
        return {
          ...fallbackResponse,
          modelUsed: FALLBACK_MODELS.fallback,
          cost: 0,
          sessionCost: sessionCost
        };
      } catch (fallbackError) {
        console.error(`[AI] Fallback error:`, fallbackError);
        throw fallbackError;
      }
    }
    
    throw error;
  }
}

/**
 * Reset session cost tracking
 */
export function resetSessionCost() {
  sessionCost = 0;
}

/**
 * Generate embeddings for text chunks
 * @param text - Text to embed
 * @returns Embedding vector
 */
export async function generateEmbedding(text: string): Promise<number[]> {
  const model = (import.meta as any).env?.VITE_EMBED_MODEL || process.env.REACT_APP_EMBED_MODEL || "text-embedding-3-small";
  
  try {
    const response = await openai.embeddings.create({
      model,
      input: text,
      encoding_format: "float"
    });
    
    return response.data[0].embedding;
  } catch (error) {
    console.error("Error generating embedding:", error);
    throw error;
  }
} 