import { chat, getSystemPrompt } from './chat';
import { getRelevantChunks } from './retriever';
import { sanitizeQuery, rephraseNegativeResponse, moderateQueryOpenAI } from './safety';

import { Chunk } from '../types';

/**
 * Interface for the answer response
 */
interface AnswerResponse {
  content: string;
  modelUsed?: string;
  sources?: string[];
  cost?: number;
  sessionCost?: number;
  error?: string;
  context?: string;
  isStrengthsWeaknesses?: boolean;
  ensembleUsed?: boolean;
}

/**
 * Similarity threshold for relevant chunks
 */
const SIM_THRESHOLD = Number((import.meta as any).env?.VITE_SIM_THRESHOLD) || 0.75;

/**
 * Enhanced strengths/weaknesses context for better responses
 */
const STRENGTHS_WEAKNESSES_CONTEXT = `
**John's Key Strengths (to highlight):**
- Unique dual background in music therapy and clinical psychology
- Strong academic performance (4.0 GPA in doctoral program)
- Diverse clinical experience across settings and populations
- Evidence-based practice competencies (CBT, CPT, EMDR, DBT, ACT)
- Research experience and statistical knowledge
- Commitment to supervision and professional development
- Multicultural competence and diverse client populations
- Strong assessment and evaluation skills

**Areas for Growth (to frame positively):**
- Developing expertise in specialized populations (frame as: "eager to deepen specialization")
- Building advanced clinical skills (frame as: "committed to ongoing skill development")
- Expanding research methodologies (frame as: "interested in collaborative research opportunities")
- Strengthening specific therapeutic modalities (frame as: "pursuing additional training in [specific area]")

**Positive Reframing Strategies:**
- Present challenges as growth opportunities with specific development plans
- Emphasize learning mindset and openness to feedback
- Connect to supervision and training goals
- Highlight transferable skills from music therapy background
- Show progression and development over time
`;

/**
 * Detect if query is about strengths/weaknesses
 */
function isStrengthsWeaknessesQuery(query: string): boolean {
  const patterns = [
    /strength/i, /weakness/i, /challenge/i, /limitation/i, /struggle/i, /difficulty/i,
    /area.*improvement/i, /grow.*area/i, /development.*need/i, /feedback/i,
    /what.*good.*at/i, /what.*excel/i, /what.*need.*work/i, /where.*improve/i,
    /positive.*aspect/i, /negative.*aspect/i, /tell.*about.*yourself/i,
    /why.*should.*choose/i, /fit.*for.*program/i, /areas.*development/i
  ];
  
  return patterns.some(pattern => pattern.test(query));
}

/**
 * Generate enhanced context for strengths/weaknesses queries
 */
function enhanceContextForStrengthsWeaknesses(chunks: Chunk[]): string {
  const baseContext = chunks
    .map(chunk => `[${chunk.id}] ${chunk.content}`)
    .join('\n');
  
  // Add specialized context for strengths/weaknesses
  const enhancedContext = `${STRENGTHS_WEAKNESSES_CONTEXT}\n\n**CV Information:**\n${baseContext}`;
  
  return enhancedContext;
}

/**
 * Generate a comprehensive answer using enhanced RAG pipeline with ensemble support
 * @param question - User's question
 * @param resumeData - Resume data for context
 * @returns Answer with citations and sources
 */
export async function generateAnswer(question: string, resumeData: any): Promise<AnswerResponse> {
  try {
    // Step 1: Sanitize the query
    const safetyResult = sanitizeQuery(question);
    
    if (!safetyResult.isSafe) {
      return {
        content: safetyResult.warnings[0] || "I'm sorry, but I cannot process that query. Please ask about John's background, experience, or qualifications.",
        error: "unsafe_query"
      };
    }

    // Step 2: OpenAI Moderation API (second layer of safety)
    const moderation = await moderateQueryOpenAI(question);
    if (moderation.flagged) {
      return {
        content: "I'm sorry, I can't discuss that topic. Please ask about John's professional background, education, or experience.",
        error: "moderation_flagged"
      };
    }

    // Step 2: Retrieve relevant chunks
    const chunks = await getRelevantChunks(question, resumeData);
    
    // Filter chunks by similarity threshold
    const relevantChunks = chunks.filter(chunk => chunk.score >= SIM_THRESHOLD);
    
    // Step 3: Check if we have enough context
    if (relevantChunks.length === 0) {
      return {
        content: "I don't currently have that information in John's CV. Please try asking about his education, clinical experience, research, or other professional background.",
        sources: []
      };
    }

    // Step 4: Detect query type and enhance context accordingly
    const isStrengthsWeaknesses = isStrengthsWeaknessesQuery(question);
    
    let context: string;
    if (isStrengthsWeaknesses) {
      context = enhanceContextForStrengthsWeaknesses(relevantChunks);
    } else {
      context = relevantChunks
        .map(chunk => `[${chunk.id}] ${chunk.content}`)
        .join('\n');
    }

    // Step 5: Get context-appropriate system prompt
    const systemPrompt = getSystemPrompt(question);

    // Step 6: Prepare messages for the model
    const messages = [
      { role: "system", content: systemPrompt },
      { role: "assistant", content: context },
      { role: "user", content: question }
    ];

    // Step 7: Generate response using enhanced model chain with ensemble support
    const response = await chat(messages, relevantChunks, question);
    
    const content = response.choices[0].message.content || "";
    
    // Step 8: Post-process response
    const processedContent = await postProcessResponse(content, isStrengthsWeaknesses);
    
    // Step 9: Determine context type for response metadata
    const contextType = determineContextType(question);
    
    // Step 10: Check if ensemble was used
    const ensembleUsed = response.modelUsed === 'ensemble';
    
    return {
      content: processedContent,
      modelUsed: response.modelUsed,
      sources: relevantChunks.map(chunk => chunk.source),
      cost: response.cost,
      sessionCost: response.sessionCost,
      context: contextType,
      isStrengthsWeaknesses,
      ensembleUsed
    };

  } catch (error: any) {
    console.error('Error generating answer:', error);
    
    // Handle specific error types
    if (error.status === 401 || error.status === 403) {
      return {
        content: "I'm sorry, but there's an authentication issue with the AI service. Please try again later.",
        error: "auth_error"
      };
    }
    
    if (error.status === 429) {
      return {
        content: "I'm currently experiencing high demand. Please try again in a moment.",
        error: "rate_limit"
      };
    }
    
    return {
      content: "I'm sorry, but I encountered an error while processing your question. Please try again.",
      error: "unknown_error"
    };
  }
}

/**
 * Determine context type from query
 */
function determineContextType(query: string): string {
  const lowerQuery = query.toLowerCase();
  
  if (lowerQuery.includes('internship') || lowerQuery.includes('appic') || lowerQuery.includes('training director')) {
    return 'internship';
  } else if (lowerQuery.includes('research') || lowerQuery.includes('academic') || lowerQuery.includes('dissertation')) {
    return 'academic';
  } else if (lowerQuery.includes('supervisor') || lowerQuery.includes('clinical') || lowerQuery.includes('therapy')) {
    return 'supervisor';
  }
  
  return 'general';
}

/**
 * Enhanced post-processing with context awareness
 */
async function postProcessResponse(content: string, isStrengthsWeaknesses: boolean = false): Promise<string> {
  let processedContent = content;
  
  // Standard response validation
  if (!validateResponse(processedContent)) {
    processedContent = rephraseNegativeResponse(processedContent);
  }
  
  // Additional processing for strengths/weaknesses responses
  if (isStrengthsWeaknesses) {
    // Ensure positive framing
    processedContent = enhanceStrengthsWeaknessesResponse(processedContent);
  }
  
  return processedContent;
}

/**
 * Enhance strengths/weaknesses responses with positive framing
 */
function enhanceStrengthsWeaknessesResponse(content: string): string {
  // Replace negative language with positive alternatives
  const positiveReplacements = [
    [/\b(weakness|weak)\b/gi, 'area for development'],
    [/\b(limitation|limited)\b/gi, 'opportunity for growth'],
    [/\b(struggle|struggling)\b/gi, 'working to improve'],
    [/\b(difficulty|difficult)\b/gi, 'learning opportunity'],
    [/\b(problem|issue)\b/gi, 'challenge to address'],
    [/\b(lack|lacking)\b/gi, 'developing expertise in'],
    [/\b(inexperience|inexperienced)\b/gi, 'gaining experience in'],
    [/\b(failure|failed)\b/gi, 'learning experience'],
    [/\b(can't|cannot)\b/gi, 'am developing skills to'],
    [/\b(don't know|doesn't know)\b/gi, 'am learning about']
  ];
  
  let enhancedContent = content;
  
  for (const [pattern, replacement] of positiveReplacements) {
    enhancedContent = enhancedContent.replace(pattern, replacement as string);
  }
  
  return enhancedContent;
}

/**
 * Validate response quality
 */
export function validateResponse(response: string): boolean {
  // Check for potential hallucination indicators
  const hallucinationPatterns = [
    /i don't have access to/i,
    /i cannot provide/i,
    /i'm not able to/i,
    /i don't have that information/i,
    /i don't know/i,
    /i'm not sure/i,
    /i can't tell/i
  ];
  
  // Check if response contains citations
  const hasCitations = /\[\w+\]/.test(response);
  
  // Check if response is too generic
  const genericPatterns = [
    /i would need more information/i,
    /it depends/i,
    /it varies/i,
    /there are many factors/i
  ];
  
  const hasHallucination = hallucinationPatterns.some(pattern => pattern.test(response));
  const isGeneric = genericPatterns.some(pattern => pattern.test(response));
  
  // Response is valid if it has citations and no hallucination indicators
  return hasCitations && !hasHallucination && !isGeneric;
}

/**
 * Format sources for display
 */
export function formatSources(chunks: Chunk[]): string[] {
  return chunks.map(chunk => chunk.source);
}

/**
 * Reset session cost tracking
 */
export function resetSessionCost(): void {
  // Note: sessionCost is managed in chat.ts
  console.info('[Answer] Session cost reset requested');
} 