import React, { useState } from 'react';
import { Section, ResumeData } from './types';
import cvData from '../cv_json_data.json';
import LeftNav from './components/LeftNav';
import CVContent from './components/CVContent';
import Chatbot from './components/Chatbot';

// Convert CV data to ResumeData format for the chatbot
const convertToResumeData = (cvData: any): ResumeData => {
  return {
    basics: {
      name: cvData.personalInfo?.name || '',
      label: 'Clinical Psychology Doctoral Student',
      image: '',
      email: cvData.personalInfo?.email || '',
      phone: cvData.personalInfo?.phone || '',
      url: '',
      summary: '',
      location: {
        address: cvData.personalInfo?.address || '',
        postalCode: '',
        city: '',
        countryCode: 'US',
        region: ''
      },
      profiles: []
    },
    education: cvData.education?.map((edu: any) => ({
      institution: edu.institution || '',
      url: '',
      area: edu.degree || '',
      studyType: edu.degree || '',
      startDate: '',
      endDate: edu.date || '',
      score: edu.details || '',
      courses: []
    })) || [],
    work: cvData.supervisedClinicalExperience?.map((exp: any) => ({
      name: exp.organization || '',
      position: exp.position || '',
      url: '',
      startDate: '',
      endDate: exp.dates || '',
      summary: '',
      highlights: exp.responsibilities || [],
      location: exp.location || '',
      supervisor: exp.supervisor || exp.supervisors?.join(', ') || '',
      bullets: exp.responsibilities || []
    })) || [],
    presentations: cvData.presentations?.map((pres: any) => ({
      name: pres.title || '',
      date: pres.date || '',
      event: pres.venue || '',
      url: '',
      summary: '',
      title: pres.title || '',
      type: pres.type || '',
      venue: pres.venue || ''
    })) || [],
    awards: cvData.honorsAndAwards?.map((award: any) => ({
      title: award.award || '',
      date: award.year || '',
      awarder: '',
      summary: '',
      description: ''
    })) || [],
    references: cvData.references?.map((ref: any) => ({
      name: ref.name || '',
      position: ref.title || '',
      organization: ref.organization || '',
      phone: ref.phone || '',
      email: ref.email || ''
    })) || [],
    content_blocks: [],
    metadata: {
      total_blocks: 0,
      source_file: 'cv_json_data.json'
    }
  };
};

// Simple error boundary for individual components
class ErrorBoundary extends React.Component<
  { children: React.ReactNode; componentName: string },
  { hasError: boolean; error?: Error }
> {
  constructor(props: { children: React.ReactNode; componentName: string }) {
    super(props);
    this.state = { hasError: false };
  }

  static getDerivedStateFromError(error: Error) {
    return { hasError: true, error };
  }

  componentDidCatch(error: Error, errorInfo: React.ErrorInfo) {
    console.error(`Error in ${this.props.componentName}:`, error, errorInfo);
  }

  render() {
    if (this.state.hasError) {
      return (
        <div className="bg-red-50 border border-red-200 rounded-lg p-4 m-4">
          <h3 className="text-red-800 font-semibold mb-2">
            {this.props.componentName} Error
          </h3>
          <p className="text-red-600 text-sm">
            Something went wrong loading this component.
          </p>
          <button
            onClick={() => window.location.reload()}
            className="mt-2 px-3 py-1 bg-red-600 text-white rounded text-sm hover:bg-red-700"
          >
            Reload Page
          </button>
        </div>
      );
    }

    return this.props.children;
  }
}

// Safe component wrapper
const SafeComponent: React.FC<{
  componentName: string;
  children: React.ReactNode;
}> = ({ componentName, children }) => {
  return (
    <ErrorBoundary componentName={componentName}>
      {children}
    </ErrorBoundary>
  );
};

const App: React.FC = () => {
  const [currentSection, setCurrentSection] = useState<string>('education');
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

  // Convert CV data to ResumeData format for chatbot
  const resumeData = convertToResumeData(cvData);

  // Convert CV data to sections
  const sections: Section[] = [
    { id: 'education', title: 'Education', icon: 'graduation-cap', order: 1 },
    { id: 'supervisedClinicalExperience', title: 'Supervised Clinical Experience', icon: 'briefcase', order: 2 },
    { id: 'evidenceBasedProtocols', title: 'Evidence-Based Protocols', icon: 'award', order: 3 },
    { id: 'supervisoryExperience', title: 'Supervisory Experience', icon: 'users', order: 4 },
    { id: 'additionalClinicalExperience', title: 'Additional Clinical Experience', icon: 'heart', order: 5 },
    { id: 'researchExperience', title: 'Research Experience', icon: 'book-open', order: 6 },
    { id: 'teachingExperience', title: 'Teaching Experience', icon: 'presentation', order: 7 },
    { id: 'honorsAndAwards', title: 'Honors & Awards', icon: 'trophy', order: 8 },
    { id: 'presentations', title: 'Presentations', icon: 'presentation', order: 9 },
    { id: 'administrativeRoles', title: 'Administrative Roles', icon: 'briefcase', order: 10 },
    { id: 'technologyTools', title: 'Technology Tools', icon: 'certificate', order: 11 },
    { id: 'professionalMemberships', title: 'Professional Memberships', icon: 'network', order: 12 },
    { id: 'trainingAndEducation', title: 'Training & Education', icon: 'certificate', order: 13 },
    { id: 'communityService', title: 'Community Service', icon: 'heart', order: 14 },
    { id: 'references', title: 'References', icon: 'user-check', order: 15 }
  ];

  const handleSectionClick = (sectionId: string) => {
    setCurrentSection(sectionId);
    setIsMobileMenuOpen(false);
    const element = document.getElementById(sectionId);
    if (element) {
      element.scrollIntoView({ behavior: 'smooth' });
    }
  };

  const handleDownloadPDF = () => {
    const link = document.createElement('a');
    link.href = '/John Britton Curriculum Vitae (CV).pdf';
    link.download = 'John_Britton_CV.pdf';
    link.click();
  };

  return (
    <div className="min-h-screen bg-white">
      {/* Header */}
      <SafeComponent componentName="Header">
        <header className="bg-primary-blue text-white shadow-lg">
          <div className="container mx-auto px-4 py-6">
            <div className="flex justify-between items-center">
              <div>
                <h1 className="text-3xl font-bold">{cvData.personalInfo.name}</h1>
                <p className="text-secondary-blue text-lg">Clinical Psychology Doctoral Student</p>
              </div>
              <div className="hidden md:flex space-x-4">
                <button
                  onClick={handleDownloadPDF}
                  className="bg-secondary-gold hover:bg-yellow-600 text-white px-4 py-2 rounded-lg font-medium transition-colors"
                >
                  Download PDF
                </button>
              </div>
              <button
                className="md:hidden text-white"
                onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
              >
                <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
                </svg>
              </button>
            </div>
          </div>
        </header>
      </SafeComponent>

      {/* Mobile Menu */}
      {isMobileMenuOpen && (
        <div className="md:hidden bg-primary-blue text-white">
          <div className="container mx-auto px-4 py-4">
            <nav className="space-y-2">
              {sections.map((section) => (
                <button
                  key={section.id}
                  onClick={() => handleSectionClick(section.id)}
                  className={`block w-full text-left px-3 py-2 rounded ${
                    currentSection === section.id
                      ? 'bg-secondary-blue text-white'
                      : 'text-gray-300 hover:text-white hover:bg-blue-700'
                  }`}
                >
                  {section.title}
                </button>
              ))}
            </nav>
          </div>
        </div>
      )}

      {/* Main Content */}
      <div className="container mx-auto px-4 py-8">
        <div className="grid grid-cols-1 lg:grid-cols-4 gap-8">
          {/* Left Navigation - Desktop */}
          <div className="hidden lg:block lg:col-span-1">
            <div className="sticky top-8 h-screen overflow-y-auto">
              <SafeComponent componentName="LeftNav">
                <LeftNav
                  sections={sections}
                  activeSection={currentSection}
                  onSectionClick={handleSectionClick}
                />
              </SafeComponent>
            </div>
          </div>

          {/* CV Content */}
          <div className="lg:col-span-2">
            <SafeComponent componentName="CVContent">
              <CVContent cvData={cvData} />
            </SafeComponent>
          </div>

          {/* Chatbot */}
          <div className="lg:col-span-1">
            <SafeComponent componentName="Chatbot">
              <Chatbot resumeData={resumeData} />
            </SafeComponent>
          </div>
        </div>
      </div>

      {/* Footer */}
      <SafeComponent componentName="Footer">
        <footer className="bg-gray-100 text-gray-600 py-8 mt-12">
          <div className="container mx-auto px-4 text-center">
            <p className="mb-2">
               2024 {cvData.personalInfo.name}. All rights reserved.
            </p>
            <p className="text-sm">
              <strong>Privacy Notice:</strong> This site uses the OpenAI API to process chatbot queries in real time. 
              No personal data beyond your queries is stored; logs are anonymized.
            </p>
          </div>
        </footer>
      </SafeComponent>
    </div>
  );
};

export default App; 