import React, { useState, useRef, useEffect } from 'react';
import { ResumeData, ChatMessage } from '../types';
import { Send } from 'lucide-react';
import { generateAnswer } from '../utils/answer';
import { sanitizeQuery } from '../utils/safety';

interface ChatbotProps {
  resumeData: ResumeData;
  isOpen?: boolean;
  onToggle?: () => void;
}

const Chatbot: React.FC<ChatbotProps> = ({ resumeData }) => {
  console.log('Chatbot component initializing...');
  console.log('Resume data:', resumeData);
  
  const [messages, setMessages] = useState<ChatMessage[]>([]);
  const [inputValue, setInputValue] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [processingStatus, setProcessingStatus] = useState<string>('');
  const messagesEndRef = useRef<HTMLDivElement>(null);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!inputValue || !inputValue.trim() || isLoading) return;

    const userMessage: ChatMessage = {
      id: Date.now().toString(),
      role: 'user',
      content: inputValue.trim(),
      timestamp: new Date(),
    };

    setMessages(prev => [...prev, userMessage]);
    setInputValue('');
    setIsLoading(true);
    setProcessingStatus('Analyzing your question...');

    try {
      // Sanitize the query
      const safetyResult = sanitizeQuery(inputValue.trim());
      
      if (!safetyResult.isSafe) {
        const errorMessage: ChatMessage = {
          id: (Date.now() + 1).toString(),
          role: 'assistant',
          content: safetyResult.warnings[0] || 'I\'m sorry, but I cannot process that query. Please ask a question about John\'s background, experience, or qualifications.',
          timestamp: new Date(),
        };
        setMessages(prev => [...prev, errorMessage]);
        setIsLoading(false);
        setProcessingStatus('');
        return;
      }

      setProcessingStatus('Searching CV information...');

      // Generate answer using enhanced pipeline
      const answer = await generateAnswer(inputValue.trim(), resumeData);
      
      if (answer.error) {
        const errorMessage: ChatMessage = {
          id: (Date.now() + 1).toString(),
          role: 'assistant',
          content: answer.content,
          timestamp: new Date(),
        };
        setMessages(prev => [...prev, errorMessage]);
        setIsLoading(false);
        setProcessingStatus('');
        return;
      }

      setProcessingStatus(answer.ensembleUsed ? 'Running specialized analysis...' : 'Generating response...');

      // Create assistant message with enhanced metadata
      const assistantMessage: ChatMessage = {
        id: (Date.now() + 1).toString(),
        role: 'assistant',
        content: answer.content,
        timestamp: new Date(),
        citations: answer.sources,
        metadata: {
          modelUsed: answer.modelUsed,
          ensembleUsed: answer.ensembleUsed,
          context: answer.context,
          isStrengthsWeaknesses: answer.isStrengthsWeaknesses,
          cost: answer.cost,
          sessionCost: answer.sessionCost
        }
      };

      setMessages(prev => [...prev, assistantMessage]);
      setIsLoading(false);
      setProcessingStatus('');

    } catch (error) {
      console.error('Error generating response:', error);
      
      const errorMessage: ChatMessage = {
        id: (Date.now() + 1).toString(),
        role: 'assistant',
        content: 'I\'m sorry, but I encountered an error while processing your question. Please try again.',
        timestamp: new Date(),
      };
      
      setMessages(prev => [...prev, errorMessage]);
      setIsLoading(false);
      setProcessingStatus('');
    }
  };



  const formatTimestamp = (timestamp: Date): string => {
    return timestamp.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
  };

  return (
    <div className="bg-white border border-gray-200 rounded-lg shadow-sm h-full flex flex-col">
      {/* Header */}
      <div className="p-4 border-b border-gray-200">
        <h3 className="font-semibold text-gray-900 mb-2">AI Assistant</h3>
        <p className="text-sm text-gray-600">
          Ask about John's background, experience, or qualifications
        </p>
      </div>
      
      {/* Messages */}
      <div className="flex-1 overflow-y-auto p-4 space-y-4">
        {messages.map((message) => (
          <div
            key={message.id}
            className={`flex ${message.role === 'user' ? 'justify-end' : 'justify-start'}`}
          >
            <div
              className={`max-w-[80%] rounded-lg px-3 py-2 ${
                message.role === 'user'
                  ? 'bg-primary-blue text-white'
                  : 'bg-gray-100 text-gray-900'
              }`}
            >
              <p className="text-sm whitespace-pre-wrap">{message.content}</p>
              
              {/* Show metadata for assistant messages */}
              {message.role === 'assistant' && message.metadata && (
                <div className="mt-2 text-xs text-gray-500">
                  {message.metadata.ensembleUsed && (
                    <span className="inline-block bg-blue-100 text-blue-800 px-2 py-1 rounded mr-2">
                      Multi-model analysis
                    </span>
                  )}
                  {message.metadata.modelUsed && message.metadata.modelUsed !== 'ensemble' && (
                    <span className="inline-block bg-green-100 text-green-800 px-2 py-1 rounded">
                      {message.metadata.modelUsed}
                    </span>
                  )}
                </div>
              )}
              
              <span className="text-xs opacity-70 mt-1 block">
                {formatTimestamp(message.timestamp)}
              </span>
            </div>
          </div>
        ))}
        
        {/* Loading indicator */}
        {isLoading && (
          <div className="flex justify-start">
            <div className="bg-gray-100 rounded-lg px-3 py-2 max-w-[80%]">
              <div className="flex items-center space-x-2">
                <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-gray-600"></div>
                <span className="text-sm text-gray-600">{processingStatus}</span>
              </div>
            </div>
          </div>
        )}
        
        <div ref={messagesEndRef} />
      </div>
      
      {/* Input */}
      <div className="p-4 border-t border-gray-200">
        <form onSubmit={handleSubmit} className="flex space-x-2">
          <input
            type="text"
            value={inputValue}
            onChange={(e) => setInputValue(e.target.value)}
            placeholder="Ask about John's background..."
            className="flex-1 px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary-blue focus:border-transparent"
            disabled={isLoading}
          />
          <button
            type="submit"
            disabled={isLoading || !inputValue.trim()}
            className="px-4 py-2 bg-primary-blue text-white rounded-lg hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed flex items-center space-x-1"
          >
            <Send size={16} />
            <span className="hidden sm:inline">Send</span>
          </button>
        </form>
      </div>
      
      {/* Privacy Notice */}
      <div className="px-4 py-3 bg-gray-50 border-t border-gray-200 rounded-b-lg">
        <div className="flex items-center space-x-2">
          <svg className="w-3 h-3 text-gray-400 flex-shrink-0" fill="currentColor" viewBox="0 0 20 20">
            <path fillRule="evenodd" d="M5 9V7a5 5 0 0110 0v2a2 2 0 012 2v5a2 2 0 01-2 2H5a2 2 0 01-2-2v-5a2 2 0 012-2zm8-2v2H7V7a3 3 0 016 0z" clipRule="evenodd" />
          </svg>
          <p className="text-xs text-gray-500 leading-relaxed">
            Queries processed via OpenAI API. No personal data stored. 
            <span className="text-gray-400 ml-1">Using implies consent.</span>
          </p>
        </div>
      </div>
    </div>
  );
};

export default Chatbot; 